.body {
  background-color: #c8abe6;
}

img {
  width: 100%;
  height: 100%;
  display: block;
}

.gallery {
  flex-wrap: wrap;
  margin-top: 60px;
  margin-bottom: 60px;
  display: flex;
  position: relative;
}

.photocard {
  width: 400px;
  height: 250px;
  object-fit: contain;
  flex-basis: calc(25% - 7.5px);
  transition-property: transform;
  transition-duration: .5s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.photocard:not(:nth-child(4n)) {
  margin-right: 10px;
}

.photocard:hover {
  transform: scale(1.1);
}

.gallery__link {
  cursor: pointer;
  height: 100%;
  text-decoration: none;
  display: block;
}

.info {
  z-index: 100;
  color: #8a2be2;
  background-color: #f0f8ff;
  border: 1px solid #eee;
  border-bottom-left-radius: 20%;
  border-bottom-right-radius: 20%;
  justify-content: space-around;
  align-items: center;
  padding: 20px 24px;
  transition: box-shadow .55s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.photocard:nth-last-child(n+5) {
  margin-bottom: 140px;
}

.info-item {
  height: 35px;
  width: auto;
  border: rgba(28, 3, 23, .12);
  border: color 2px blueviolet;
  background-color: #f0f8ff;
}

